.tooltip {
  display: inline-block;
}

.tooltip .tooltiptext {
  visibility: hidden;
  color: #fff;
  z-index: 1;
  background-color: #6b7280;
  border-radius: 6px;
  width: 100%;
  padding: 1em;
  position: absolute;
  top: 120%;
  left: 0;
}

.tooltip:hover .tooltiptext {
  visibility: visible;
}

.tooltip .tooltiptext:after {
  content: " ";
  border: 7px solid #0000;
  border-bottom-color: #6b7280;
  margin-left: -7px;
  position: absolute;
  bottom: 100%;
  left: 10%;
}
